import { Component } from '@angular/core';
import { Platform, AlertController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { TabsPage } from '../pages/tabs/tabs';
import { LoginPage } from '../pages/login/login';
import { LoginAfterPage } from '../pages/login-after/login-after';
import { QrPage } from '../pages/qr/qr';
import { ComentPage } from '../pages/coment/coment';
import { Push, PushObject, PushOptions } from '@ionic-native/push';
import { ENV } from '../config/environment';
import { FCM } from '@ionic-native/fcm';


import { SessionService } from '../services/session.service';



@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  public notificacion:any;
  rootPage:any;

  constructor(private fcm: FCM,private alertCtrl: AlertController,private push: Push, platform: Platform, statusBar: StatusBar, splashScreen: SplashScreen, public sessionS:SessionService) {
    platform.ready().then(() => {
      if (this.sessionS.getObject('user')) {
        this.rootPage = TabsPage;
      }else {
        this.rootPage = LoginPage;
      }
      
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();
      this.fcm.onNotification().subscribe(data => {
        if(data.wasTapped){
          
          let alert = this.alertCtrl.create({
            title: 'Low battery',
            subTitle: data.message + ",11111,"  + data.title + ","  + data.count+ ","  + data.sound+ ","  + data.image + "," + data.actions[0].title,
            buttons: ['Dismiss']
          });
        } else {
          this.sessionS.setObject('dataNotification1',data)

          console.log("Received in foreground");
        };
      });
      this.hola();
    });
  }

  approve(){
    let alert = this.alertCtrl.create({
      title: 'Low battery',
      subTitle: '1',
      buttons: ['Dismiss']
    });
    alert.present();
  }
  reject(){
    let alert = this.alertCtrl.create({
      title: 'Low battery',
      subTitle: '2',
      buttons: ['Dismiss']
    });
    alert.present();
  }

  hola(){
    
    const options: PushOptions = ENV.pushFB.PushOptions;     
    const pushObject: PushObject = this.push.init(options);
    pushObject.on('notification').subscribe((notification: any) => {
      this.notificacion = notification;
      this.sessionS.setObject('dataNotification',notification)
      let alert = this.alertCtrl.create({
        title: 'Low battery',
        subTitle: notification.message + ","  + notification.title + ","  + notification.count+ ","  + notification.sound+ ","  + notification.image + "," + notification.additionalData,
        buttons: ['Dismiss']
      });
      alert.present();
      console.log('Received a notification', notification)
    });

   
    pushObject.on('registration').subscribe((registration: any) => {         
      this.sessionS.setObject('device', registration.registrationId);
    });     
    pushObject.on('error').subscribe(error => console.error('Error with Push plugin', error));    
}
}




